.circle-box{
  float:left;
  width:200px;
  margin:20px 0px 0px 25px;
  text-align: center;
}

.circle-box h3{
  color:#ffffff;
  margin-top:-20px;
}
.circle {
    width: 200px;
    height: 200px;
    display: inline-block;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
  }

  .circle:before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 200px;
    height: 0;
  }

  .circle:after {
    content: attr(data-percent);
    position: absolute;
    font-size: 2.5em;
    text-align: center;
    width: 100%;
    top: 50%;
    bottom: 0;
    left: 0;
  }

  .circle.progress {
    border: 3px solid white;
  }

  .circle.progress:before {
    background-color: #c2e8fb;
  }
  .circle.progress:after {
    color: white;
  }

/*Animations */
  
  @-webkit-keyframes fillin1 {
    0% {
      height: 0;
    }
    100% {
      height: 50%;
    }
  }
  
  @keyframes fillin1 {
    0% {
      height: 0;
    }
    100% {
      height: 50%;
    }
  }
  .circle[data-percent="1%"]:before {
    -webkit-animation: 1s fillin1 ease forwards;
            animation: 1s fillin1 ease forwards;
